.page-careers {

	$bem: '.header';
	// .header
	// 	#{$bem}-slide
	// 		--pt: 5.625rem

	$bem: '.preference';
	.preference {
		--m: 2.1875rem 0 11.875rem;
		@include lg {
			margin: 2.1875rem 0 7.875rem; }
		@include sm {
			margin: 2.1875rem 0 1.875rem; }
		#{$bem}__box-header {
			margin-bottom: 6.25rem; }
		#{$bem}__header {
			@include sm {
				margin-bottom: 1.875rem; }
			&.grid {}
			.grid-item {
				&--title {}
				&--text {
					max-width: 320px;
					@include lg {
						max-width: 100%; } } } }
		#{$bem}__box-content {} }


	$bem: '.quotation';
	.quotation {
		.container {
			padding-top: 6.5625rem;
			padding-bottom: 16.25rem;
			@include sm {
				padding-top: 8.5625rem;
				padding-bottom: 9.25rem; } }
		.detail {
			&.detail-1 {
				--t: initial;
				--b: 2.5rem;
				--r: -31.5rem;
				@include lg {
					right: -10.625rem;
					top: 0rem; }
				@include sm {
					right: -6.625rem; } }
			&.detail-2 {
				--b: 10.625rem;
				--l: -38.625rem;
				@include lg {
					left: -6.875rem;
					bottom: 5rem; }
				@include sm {
					bottom: 0; } } } }



	.gallery-masonry {
		--m: 1.875rem 0 18.75rem; }


	.other-jobs {
		--p: 0;
		@include xl {
			padding-bottom: 0; } }


	.careers-blog-small {} }

